<template>
  <fieldset v-show="!hidePrivileges" class="form--block">
    <legend class="sub-header">  </legend>

    <fieldset v-show="showDetails" class="form--block">
      <p class="form--text">
           <i> '  -     </i>,  <b
        class="form--text__bold"> </b>  <abbr title=" "></abbr>
         '   2500 .
      </p>

      <legend class="sub2-header"> ' </legend>
      <div class="form--row">
        <input v-model="detailVolume"
               value=" 2500 "
               class="form--radio"
               id="more-2500"
               type="radio"
               name="detail-volume">
        <label class="form--label form--label__more-2500" for="more-2500"> 2500 </label>
        <input v-model="detailVolume"
               value=" 2500 "
               class="form--radio"
               id="less-2500"
               type="radio"
               name="detail-volume">
        <label class="form--label form--label__less-2500" for="less-2500"> 2500 </label>
      </div>
    </fieldset>

    <div v-show="!hidePrivilegesBtns">

      <fieldset>
        <legend v-show="showDetails" class="sub2-header"></legend>
        <div class="form--row">
          <input @change="changePrivilegesValue($event.target.value)"
                 class="form--radio"
                 ref="privilege-no"
                 id="privilege-no"
                 type="radio"
                 name="privilege"
                 :value="false">
          <label class="form--label form--label__privileges" for="privilege-no">
            <div class="form--icon-wrap ">
              <svg class="form--icon form--icon__small form--icon__small-not" aria-hidden="true" viewBox="0 0 129 129">
                <use xlink:href="/Content/img/icons.svg#no-privilege"></use>
              </svg>
              <p class="label--text"></p>
            </div>
          </label>

          <input @change="changePrivilegesValue($event.target.value)"
                 class="form--radio"
                 ref="privilege-pensioner"
                 id="privilege-pensioner"
                 type="radio"
                 name="privilege"
                 value="">
          <label class="form--label form--label__privileges" for="privilege-pensioner">
            <div class="form--icon-wrap ">
              <svg class="form--icon form--icon__small form--icon__small-not" aria-hidden="true"
                   viewBox="0 0 512.001 512.001">
                <use xlink:href="/Content/img/icons.svg#old"></use>
              </svg>
              <p class="label--text"></p>
            </div>
          </label>

          <input @change="changePrivilegesValue($event.target.value)"
                 class="form--radio"
                 ref="privilege-veteran"
                 id="privilege-veteran"
                 type="radio"
                 name="privilege"
                 value="">
          <label class="form--label form--label__privileges" for="privilege-veteran">
            <div class="form--icon-wrap ">
              <svg class="form--icon form--icon__small form--icon__small-not" aria-hidden="true" viewBox="0 0 496 496">
                <use xlink:href="/Content/img/icons.svg#veteran"></use>
              </svg>
              <p class="label--text"></p>
            </div>
          </label>

          <input @change="changePrivilegesValue($event.target.value)"
                 class="form--radio"
                 ref="privilege-invalid"
                 id="privilege-invalid"
                 type="radio"
                 name="privilege"
                 value="  ">
          <label class="form--label form--label__privileges" for="privilege-invalid">
            <div class="form--icon-wrap ">
              <svg class="form--icon form--icon__small form--icon__small-not" aria-hidden="true" viewBox="0 0 496 496">
                <use xlink:href="/Content/img/icons.svg#disabled"></use>
              </svg>
              <p class="label--text">  </p>
            </div>
          </label>

          <input @change="changePrivilegesValue($event.target.value)"
                 class="form--radio"
                 ref="privilege-chernobyl"
                 id="privilege-chernobyl"
                 type="radio"
                 name="privilege"
                 value="  ">
          <label class="form--label form--label__privileges" for="privilege-chernobyl">
            <div class="form--icon-wrap ">
              <svg class="form--icon form--icon__small form--icon__small-not" aria-hidden="true"
                   viewBox="0 0 461.806 461.806">
                <use xlink:href="/Content/img/icons.svg#radiation"></use>
              </svg>
              <p class="label--text">  </p>
            </div>
          </label>
        </div>
      </fieldset>

      <template v-show="showPrivilegesText">
        <div class="form--text">
          <h5 class="form--header__privileges">a    ,  :</h5>
          <ul class="form--list">
            <li>'  <abbr title=" "></abbr>  2500 </li>
            <li><abbr title=" "></abbr>      </li>
            <li> <abbr title=" "></abbr>  <b
              class="form--text__bold"></b> ,     
            </li>
            <li> <abbr title=" "></abbr>
                       
            </li>
          </ul>
          <a href="https://zakon.rada.gov.ua/laws/show/1961-15" target="_blank"
             class="form--link form--link__privileges"> </a>
        </div>

        <div class="form--text">
          <h5 class="form--header__privileges">    </h5>
          <p>    ,   <b class="form--text__bold">  50%    </b>
             </p>
        </div>
      </template>
    </div>
  </fieldset>
</template>

<script>
  import {mapGetters} from "vuex";
  import {mapActions} from "vuex";

  export default {
    data() {
      return {
        detailVolume: " 2500 "
      }
    },
    computed: {
      ...mapGetters("privileges", {
        privileges: "privileges"
      }),
      ...mapGetters("user", {
        isLegalEntity: "isLegalEntity"
      }),
      ...mapGetters("transport", {
        transType: "transType",
        transExtraName: "transExtraName"
      }),
      hidePrivileges() {
        return this.isLegalEntity === true ||
          (this.transType === " " && this.transExtraName === "B 4") ||
          this.transType === "" ||
          this.transType === " " ||
          this.transType === ""

      },
      showDetails() {
        return this.transType === " " && this.transExtraName === "B 3"
      },
      hidePrivilegesBtns() {
        return this.detailVolume === " 2500 "
      },
      showPrivilegesText() {
        return this.privileges
      }
    },
    methods: {
      ...mapActions("privileges", {
        changePrivilegesValue: "changePrivilegesValue"
      }),
      resetPrivileges() {
        this.changePrivilegesValue(false);
      },
      setChecked() {
        for (let key in this.$refs) {
          if (this.$refs[key].value === this.privileges.toString()) {
            this.$refs[key].checked = true;
          }
        }
      }
    },
    mounted() {
      this.setChecked();
    },
    updated() {
      if (this.hidePrivileges || this.hidePrivilegesBtns) {
        this.resetPrivileges();
        this.setChecked();
      }
    }
  }
</script>

<style lang="less">
  .form {
    &--header {
      &__privileges {
        font-size: 25px;
        margin-bottom: 5px;
        @media only screen and (max-width: 576px) {
          line-height: 1;
        }
      }
    }
    &--link {
      &__privileges {
        display: block;
      }
    }
    &--label {
      &__privileges {
        @media only screen and (max-width: 1195px) {
          height: 122px;
        }
        @media only screen and (max-width: 320px) {
          height: 132px;
        }
      }
    }
  }
</style>



